module.exports = " <div class=input-box v-cloak> <h1>Text input box</h1> <p>在<span class=point-text> input </span>标签上设置<span class=point-text> class=\"input-text\" </span>, 即可实现输入框效果。</p> <h3>文本框大小</h3> <p>文本框的大小由<span class=point-text> .input-text-lg </span>以及<span class=point-text> .input-text-sm </span>控制。</p> <p><span class=point-text>注意: 如需输入的文字与placeholder默认文字的颜色不同, 需单独设计, 此处涉及到css权重的问题。</span></p> <p>代码示例:</p> <div class=code> <div class=examples> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text input-text-lg\"</span> <span class=cn>placeholder=</span><span class=cv>\"请输入内容\"</span><span class=d>&gt;</span><br> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text\"</span> <span class=cn>placeholder=</span><span class=cv>\"请输入内容\"</span><span class=d>&gt;</span><br> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text input-text-sm\"</span> <span class=cn>placeholder=</span><span class=cv>\"请输入内容\"</span><span class=d>&gt;</span> </div> </div> <p>效果图:</p> <div class=examples> <div class=input-widgets> <input type=text class=\"input-text input-text-lg\" placeholder=请输入内容> <input type=text class=input-text placeholder=请输入内容> <input type=text class=\"input-text input-text-sm\" placeholder=请输入内容> </div> </div> <h3>错误提示文本框</h3> <p>如需使用错误提示, 只需增设<span class=point-text> .wrong-input </span>即可。</p> <p><span class=point-text>注意: 如想去除input前后空格, 可使用float定位或者提示标签和input标签在同一行。</span></p> <p>代码示例:</p> <div class=code> <div class=examples> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text wrong-input\"</span> <span class=cn>value=</span><span class=cv>\"我是错误内容\"</span><span class=d>&gt;</span><span class=d>&lt;span</span>&gt;<span class=v>错误提示</span><span class=d>&lt;/span&gt;</span> </div> </div> <p>效果图:</p> <p>获取焦点后查看效果。</p> <div class=examples> <div class=input-widgets> <input type=text class=\"input-text wrong-input\" autofocus=autofocus value=我是错误内容><span>错误提示</span> </div> </div> <h3>禁用文本框</h3> <p>如需禁用文本框, 只需增设<span class=point-text> .disable-input-text </span>即可。</p> <p><span class=point-text>注意: 也可以使用input标签的属性: disabled、readonly。</span></p> <p>代码示例:</p> <div class=code> <div class=examples> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text disable-input-text\"</span> <span class=cn>value=</span><span class=cv>\"禁用\"</span><span class=d>&gt;</span><br> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text\"</span> <span class=cn>disabled=</span><span class=cv>\"disabled\"</span> <span class=cn>value=</span><span class=cv>\"禁用\"</span><span class=d>&gt;</span><br> <span class=d>&lt;input</span> <span class=cn>type=</span><span class=cv>\"text\"</span> <span class=cn>class=</span><span class=cv>\"input-text\"</span> <span class=cn>readonly=</span><span class=cv>\"readonly\"</span> <span class=cn>value=</span><span class=cv>\"禁用\"</span><span class=d>&gt;</span> </div> </div> <p>效果图:</p> <div class=examples> <div class=input-widgets> <input type=text class=\"input-text disable-input-text\" placeholder=\"禁用-->pointer-evens\"> <input type=text class=\"input-text disable-input-text\" disabled=disabled placeholder=禁用--disabled> <input type=text class=\"input-text disable-input-text\" readonly=readonly placeholder=禁用--readonly> </div> </div> </div> ";


